* {
    box-sizing: border-box;
}

::selection {
    background-color: $clr4;
    color: #fff;
}

html {
    font-family: montserrat, sans-serif;
    font-size: rem(18px);
    color: $clr2;
    line-height: 1.3;
}

body {
    margin: 0;
    background-color: #ff98005e;
}

h1,h2,h3,h4,h5,h6 {
    font-family: caveat, sans-serif;
    font-weight: normal;
    line-height: 1;
    margin: 0 0 0.3em;
}