.book-data {
    // display: inline;
    margin-left: 10px;
    gap: 10px;
    .content {
        border: 1px solid gray;
        border-radius: 3px;
        padding: 10px;
    }
    input {
        width: 30px;
        height: 30px;
    }
    h2 {
        font-weight: 500;
        max-width: 250px;
        height: 30px;
    }
    .long {
        font-size: rem(24px);
        overflow-y: scroll;
        scroll-behavior: smooth;
        /* width */
        &::-webkit-scrollbar {
            width: 10px;
        }
        /* Track */
        &::-webkit-scrollbar-track {
            background: #555;
            border-radius: 30px;
        }
        /* Handle */
        &::-webkit-scrollbar-thumb {
            background: #f1f1f1f1;
            border-radius: 30px;
            border: 1px solid black;
        }
        /* Handle on hover */
        &::-webkit-scrollbar-thumb:hover {
            background: #888;
        }
    }
    hr {
        width: 100%;
    }
    .author {
        font-family: "Courier New", Courier, monospace;
        color: gray;
        font-size: 23px;
    }
    .first {
        font-size: 40px;
        height: 100%;
        cursor: pointer;
        transition: 1s;
    }
    .last {
        font-size: 40px;
        height: 100%;
        cursor: pointer;
        transition: 1s;
    }
    .book-details {
        width: 300px;
        height: 147px;
        overflow-y: scroll;
        scroll-behavior: smooth;
        padding-right: 10px;
        margin: 0;
        /* width */
        &::-webkit-scrollbar {
            width: 10px;
        }

        /* Track */
        &::-webkit-scrollbar-track {
            background: #555;
            border-radius: 30px;
        }

        /* Handle */
        &::-webkit-scrollbar-thumb {
            background: #f1f1f1f1;
            border-radius: 30px;
            border: 1px solid black;
        }

        /* Handle on hover */
        &::-webkit-scrollbar-thumb:hover {
            background: #888;
        }
    }
}
